body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}
.col p {
  margin-top: 18px;
}

div {
  font-size: 20px;
}

div h1 {
  text-align: center;
  margin-bottom: 10px;
}

.Jumbotron {
  background-image: url("../src/resources/jumbotran.jpg");
}

.footer {
  text-align: center;
  font-weight: 200;
  font-size: 15px;
  bottom: 3px;
  width: 100%;
  min-height: 50px;
}

.icons img {
  width: 30px;
  height: 30px;
  margin: 10px;
}

#top {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  outline: none;
  background-color: rgb(26, 8, 182);
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 50%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
